				// this.savedata.superviseCheckPhotoList = safetyTaskPhotoList.slice(0)
				var msg = {
					"checkDescribe":this.checkDescribe,
					"checkOrgId": "5e5d857c5c37ff28015c39ccf9d30019",
					"checkOrgName": "深圳市深燃石油气有限公司",
					"checkResult":this.checkResult,
					"serverFlag": "NORMAL",
					"superviseCheckPhotoList":safetyTaskPhotoList.slice(0),
					"superviseId":this.yinhuanhechamsg.id
				}
				
				console.log(11)
				console.log(msg)
				
				this.$request({
					url: 'saveCheckOpinion',
					method: 'POST',
					showLoading:true,
					loadingText:'正在提交',
					data:msg,
					success:(jsonData) => {
						console.log(6666)
						console.log(jsonData)
						if(jsonData.status){
							uni.showToast({
								title:jsonData.msg,
								duration: 1000
							});
							setTimeout(() =>{
								uni.navigateBack({
									delta:2
								})
							},1000)
						}else{
							uni.showToast({
								title:jsonData.msg,
								duration: 1000
							});
						}
					},
					complete() {
						view.loading = false;
					}
				});
				 
			},
 			shangbao:function(){
 				console.log(this.savedata)
				
				this.$refs.imgbox.upload((paths) => {
